import React from 'react';
import Link from 'next/link';
import { withRouter } from 'next/router';
import global from 'data/global';
import menus from 'data/menu';
import styles from './navigator.less';

class Navigator extends React.Component {
  state = {
    scrollTop: 0,
  };

  componentDidMount() {
    window.addEventListener('scroll', this.scroll, false);
    this.scroll();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.scroll);
  }

  scroll = () => {
    this.setState({
      scrollTop: document.documentElement.scrollTop || document.body.scrollTop,
    });
  };

  render() {
    const { router } = this.props;
    const { scrollTop } = this.state;
    const { route } = router;
    const isWhite = ['/detail'].includes(route) || scrollTop > 50;

    return (
      <div id="header" className={`${styles.navigator} ${isWhite && styles.notTop}`}>
        <Link href="/">
          <div className={styles.logo}>
            <img
              className={styles.tdlLogo}
              src={require('@/static/images/logo-footer.png')}
              alt=""
            ></img>
            <span className={styles.sub}>{global.companyDesc}</span>
          </div>
        </Link>
        <div className={styles.menus}>
          {menus.map(item => {
            const active =
              item.path === router.pathname ||
              (item.relation && item.relation.includes(router.pathname));
            return (
              <Link key={item.path} href={item.path}>
                <a className={active ? styles.active : styles.notActive}>{item.text}</a>
              </Link>
            );
          })}
        </div>
        <a className={styles.phone} href={`tel:${global.companyPhone}`}>
          <i className="iconfont icondianhua" />
          {global.companyPhone}
        </a>
      </div>
    );
  }
}

export default withRouter(Navigator);
